.container {
  background: var(--app-bg-home);
  height: 100%;
  display: grid;
  grid-template:
    'header header' auto
    'nav intro' 1fr
    'footer footer' auto / auto 50%;
}

.header {
  grid-area: header;
}

.logo {
  width: 48px;
  margin-right: 0.5rem;
}

.buildInfo {
  composes: blend-text from '~styles/text.css';
  font-size: 0.8rem;
  opacity: 0.4;
  margin-left: 1rem;
}

.nav {
  grid-area: nav;
}

.intro {
  composes: blend-text from '~styles/text.css';
  grid-area: intro;
  font-weight: bold;
  overflow: hidden;
}

.intro a {
  text-decoration: underline;
}

.social {
  grid-area: footer;
  display: flex;
  align-content: center;
  justify-content: space-between;
}

.low {
  filter: contrast(100) saturate(0);
  mix-blend-mode: overlay;
  opacity: 0.33;
}

.low:hover {
  filter: invert(1);
  mix-blend-mode: normal;
  opacity: 1;
}

.socialLink {
  display: inline-block;
  margin-right: 20px;
}

.socialIcon {
  composes: low;
  height: 48px;
}

.btn {
  display: inline-block;
  margin-bottom: 15px;
}

.socialRight {
  display: flex;
  align-items: center;
}

.credits {
  composes: blend-text from '~styles/text.css';
}

.credits a {
  font-weight: bolder;
}
